.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__carousel {
    width: 100%;
    z-index: -1;
    margin-bottom: -325px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 40%, transparent 100%);
}

.home__row {
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    z-index: 1;
}